<template>
<el-container>
  <el-header>登录</el-header>
<el-main>
<div style="margin:1rem auto; width:60%">
    <el-form  ref="form" :model="form" >
        <el-form-item label="用户名">
            <el-input v-model="form.userName"></el-input>
        </el-form-item>
        <el-form-item label="密码">
            <el-input type="password" v-model="form.password" prop="pass"></el-input>
        </el-form-item>
        <el-form-item>
            <el-button type="primary" @click="onSubmit">登录</el-button>
            <el-button @click="signUp()">注册</el-button>
        </el-form-item>
</el-form>
</div>

  </el-main>
</el-container>
  
</template>

<script>
import AV from 'leancloud-storage'
export default {
    name: 'login',
    data() {
        return {
            form: {
                userName: '',
                password: ''

            }
        }
    },
    methods: {
        onSubmit() {
            var that = this
            AV.User.logIn(this.form.userName, this.form.password).then(function (loginedUser) {
                //console.log('111', loginedUser);
                //登录成功获取点位数据
                that.getMarkData()
                that.$router.push({
                    path: 'index'
                })
            }, function (error) {
                that.$message.error('登录错误!请检查用户名和密码!');
            });
        },
        signUp() {
            this.$router.push({
                path: 'sign'
            })
        },
        getMarkData() {
            // let cql = 'select * from markObj where userName = \'' + this.form.userName + '\'';
            // AV.Query.doCloudQuery(cql).then(function (data) {
            //     let results = data.results;
            //     if (results.length > 0) {
            //         for (let i = 0; i < results.length; i++) {  //循环遍历
            //             localStorage.setItem('id' + results[i].attributes.key, results[i].id)
            //             localStorage.setItem(results[i].attributes.key, results[i].attributes.value)
            //         }
            //     }

            //     //console.log('222', results)
            // }, function (error) {
            // });
            let query = new AV.Query("markObj");    //最多查3000个
            query.equalTo("userName", this.form.userName);
            query.count().then(
                function (count) {
                    //console.log("111", count);
                    if (count <= 1000) {   //小于1000
                        query.limit(1000);
                        query.find().then(function (results) {
                            if (results.length > 0) {
                                for (let i = 0; i < results.length; i++) {  //循环遍历
                                    
                                    localStorage.setItem('id' + results[i].attributes.key, results[i].id)
                                    localStorage.setItem(results[i].attributes.key, results[i].attributes.value)
                                }
                            }
                            //console.log("3331", results.length);
                        });
                    } else if (count > 1000 && count <= 2000) {
                        query.limit(1000);
                        query.find().then(function (results) {
                            if (results.length > 0) {
                                for (let i = 0; i < results.length; i++) {  //循环遍历
                                    localStorage.setItem('id' + results[i].attributes.key, results[i].id)
                                    localStorage.setItem(results[i].attributes.key, results[i].attributes.value)
                                }
                            }
                            console.log("3332", results.length);
                        });
                        query.limit(count - 1000);
                        query.skip(1000);
                        query.find().then(results => {
                            if (results.length > 0) {
                                for (let i = 0; i < results.length; i++) {  //循环遍历
                                    localStorage.setItem('id' + results[i].attributes.key, results[i].id)
                                    localStorage.setItem(results[i].attributes.key, results[i].attributes.value)
                                }
                            }
                            //console.log("3333", results.length);
                        });
                    } else if (count > 2000 && count <= 3000) {
                        query.limit(1000);
                        query.find().then(function (results) {
                            if (results.length > 0) {
                                for (let i = 0; i < results.length; i++) {  //循环遍历
                                    localStorage.setItem('id' + results[i].attributes.key, results[i].id)
                                    localStorage.setItem(results[i].attributes.key, results[i].attributes.value)
                                }
                            }
                            //console.log("3334", results.length);
                        });
                        query.limit(1000);
                        query.skip(1000);
                        query.find().then(results => {
                            if (results.length > 0) {
                                for (let i = 0; i < results.length; i++) {  //循环遍历
                                    localStorage.setItem('id' + results[i].attributes.key, results[i].id)
                                    localStorage.setItem(results[i].attributes.key, results[i].attributes.value)
                                }
                            }
                            //console.log("3335", results.length);
                        });
                        query.limit(count - 2000);
                        query.skip(2000);
                        query.find().then(result => {
                            if (results.length > 0) {
                                for (let i = 0; i < results.length; i++) {  //循环遍历
                                    localStorage.setItem('id' + results[i].attributes.key, results[i].id)
                                    localStorage.setItem(results[i].attributes.key, results[i].attributes.value)
                                }
                            }
                            //console.log("3336", results.length);
                        });
                    }
                },
                function (error) {
                    console.log("222", error);
                }
            );
        }
    }

}
</script>

<style>
.el-header,
.el-footer {
  /* background-color: #b3c0d1; */
  /* color: #333; */
  text-align: center;
  line-height: 6rem;
}

.el-aside {
  background-color: #d3dce6;
  color: #333;
  text-align: center;
  line-height: 20rem;
}

.el-main {
  /* background-color: #e9eef3; */
  /* color: #333; */
  text-align: center;
  line-height: 16rem;
}

body > .el-container {
  margin-bottom: 4rem;
}

.el-container:nth-child(5) .el-aside,
.el-container:nth-child(6) .el-aside {
  line-height: 26rem;
}

.el-container:nth-child(7) .el-aside {
  line-height: 32rem;
}
</style>




